import React, { Component } from 'react';
import './index.css';

// Додаємо модалку
import Modal from '../Modal';
import ModalContent from '../ModalContent';

class Image extends Component {
    constructor(props) {
        super(props);
        this.state = {showModal: false, url: ""};
      
        this.handleShow = this.handleShow.bind(this);
        this.handleHide = this.handleHide.bind(this);
    };
    
    handleShow(e) {       
        const { param } = e.target.dataset;
        this.setState({showModal: true, url: param});       
    };
      
    handleHide() {
        this.setState({showModal: false});
    };

    render() {        
        const { url, x, y, year } = this.props.item;

        const newYear = year.toString().slice(0,4);

        const modal = this.state.showModal 
            ? (
                <Modal>
                    <ModalContent onClick={this.handleHide} src={this.state.url} />            
                </Modal>
              )
            : null;

        return (
            <div>
                <div className="conPic">
                    <img className="picture" src={url} alt={url.toString()} data-param={url} onClick={this.handleShow} />
                    <div className="middle">
                        <div className="textPic">{x}X{y}</div>
                        <div className="textPic">{ newYear }</div>
                    </div>
                </div>
            {modal}
            </div>      
      );
    }; 
};
  
export default Image;


