import React, { Component } from 'react';
import './index.css';

export default class Palette extends Component {  
    render() {
      return (
        <div className="boxPalette">
          <img className="palette" src="https://image.ibb.co/gqJr5J/v.jpg" alt="palette" />
        </div>
      )
    }
};


