import React, { Component } from 'react';
import './index.css';

class Footer extends Component {
    constructor(props) {
        super(props);
        this.state = { classFix: "" };
    };
    
    componentDidMount() {
        let scrollHeight = Math.max(
            document.body.scrollHeight, document.documentElement.scrollHeight,
            document.body.offsetHeight, document.documentElement.offsetHeight,
            document.body.clientHeight, document.documentElement.clientHeight
        );

        let clientHight = document.documentElement.clientHeight;

        if(clientHight >= scrollHeight) this.setState({ classFix: "fix" });
    };

    render() {
        return (           
            <footer className={ this.state.classFix }>
                <div className="text">Copyright © 2018 Tetiana  Semaniuk. All Rights Reserved.</div>
                <div className="social">       
                    <a href="https://www.instagram.com/tania_sem_" target="_blanck" rel="noopener"><div class="icon instagram"></div></a>
                    <a href="https://www.facebook.com/tsemaniukart" target="_blanck" rel="noopener"><div class="icon facebook"></div></a>
                    <a href="https://www.etsy.com/shop/TSemaniukArtSpace" target="_blanck" rel="noopener"><div class="icon etsy"></div></a>   
                </div>
            </footer>                              
        )        
    };
};

export default Footer;


