import React, { Component } from 'react';
import './index.css';

import Images from '../Images';
import Palette from '../Palette';
import Loading from '../Loading';

import Footer from '../Footer';

class Nightscapes extends Component {
    constructor(props) {
        super(props);
        this.state = { url: [], show: false };
    };

    componentDidMount() {        
        fetch('/api/v1/portfolio/nightscapes')
        .then(response => {
            return response.json();
          }).then(data => {
            this.setState({ url: data.images, show: true });
          }).catch(function(err) {
            console.log(err)
          });     
    };

    render() {               
        return (
            <div> 
                <Palette />
                { this.state.show ? <Images urls={this.state.url} /> : <Loading /> }
                <Footer />
            </div>
        )        
    };
};

export default Nightscapes;


